.App {
    text-align: center;
}

.centerImg {
    width: 272px;
    height: 92px;
    margin-bottom: 20px;
    margin-left: 100px;
}

#nav {
    font-size: 50pt;
    text-align: center;
    padding-bottom: 20pt;
    padding-top: 20pt;
}

#nav ul {
    width: 100%;
    height: 50px;
    flex-direction: row;
    list-style: none;
    margin: 0;
    padding: 50px;
    background-color: antiquewhite;
}

#nav li {
    display: block;
    float: left;
    padding: 5px;
    height: 50px;
}

#content {
    background-color: aquamarine;
    width: 100%;
    height: 800px;
}